import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";
import { Flex, List } from 'antd-mobile'

import { getMyInfo, logout } from '../../lib/api'
import { withRouter } from 'react-router-dom';
import './style.css'

const bg1 = '/static/volunteer/auditBg.png'
const bg2 = '/static/volunteer/hua.png'
const bg3 = '/static/volunteer/xuanze_2.png'

const _prefix = window._config._prefix.link
const Item = List.Item;
class Application extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        step: 0,
        userName: '',
        ready: false,
        notice: ''
      };
    }

    async componentWillMount(){
      let ret = await getMyInfo()
      if (ret && ret.code) {
        let curStep 
        switch(ret.data.level) {
          case 'apply':
            curStep = 0
            break;
          case 'audition':
            curStep = 1
            break;
          case 'training':
            curStep = 2
            break;
          case 'eliminate':
            curStep = 4
            break;
          default:
            this.props.history.push(`${_prefix}/daily`)
            return false
        }

        console.log(ret.data.training)
        this.setState({
          step : curStep,
          ready: true,
          userName : ret.data.name,
          notice : ret.data.notice,
          training : ret.data.training
        });
      }
    }

    tologout = async () => {
      let ret = await logout()
      if (ret && ret.code === true) {
        this.props.history.replace(`${_prefix}/volunteer`)
      }
    }

    render() {
        let text0 = (
            <Flex style={{width:'100vw',height:'32vw'}}>
              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'21vw'}}
              >
                <span className="textContent">资料审核中</span>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">面试环节</span>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">欢迎进入培训阶段</span>
              </Flex>
            </Flex>
          )

        let text1 = (
            <Flex style={{width:'100vw',height:'32vw'}}>
              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'21vw'}} direction='column'
              >
                <span className="textContent">通过审核</span>
                <img src={bg3} style={{width:'4.7vw',height:'4vw',marginTop:'2vw'}}  alt="正在加载过程中"/>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw'}}
              >
                <span className="textContent">进入面试</span>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">培训环节</span>
              </Flex>
            </Flex>
          )

        let text2 = (
            <Flex style={{width:'100vw',height:'32vw'}}>
              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'21vw'}} direction='column'
              >
                <span className="textContent">通过审核</span>
                <img src={bg3} style={{width:'4.7vw',height:'4vw',marginTop:'2vw'}}  alt="正在加载过程中"/>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw'}} direction='column'
              >
                <span className="textContent">通过面试</span>
                <img src={bg3} style={{width:'4.7vw',height:'4vw',marginTop:'2vw'}}  alt="正在加载过程中"/>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw'}}
              >
                <span className="textContent">培训环节</span>
              </Flex>
            </Flex>
          )

        let text3 = (
            <Flex style={{width:'100vw',height:'32vw'}}>
              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'21vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">资料审核中</span>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">面试环节</span>
              </Flex>

              <Flex 
                className="textCard" align='center' justify='center'
                style={{marginLeft:'13vw',background:'rgba(112,112,112,0.54)'}}
              >
                <span className="textContent">欢迎进入培训阶段</span>
              </Flex>
            </Flex>
          )

        let content0 = (
          <span style={{fontSize:'1.2em',lineHeight:'25px'}}>您的资料正在审核中，审核通过会以电话方式通知</span>
          )

        let content1 = this.state.notice ? (
            <span style={{fontSize:'1.2em',lineHeight:'25px'}}>
            恭喜你已通过资料审核，进入面试环节<br/>
            {this.state.notice}</span>
          ) : (
            <div>
              <span style={{fontSize:'1.2em',lineHeight:'25px'}}>恭喜你已通过资料审核，进入面试环节</span>
              <p className="nullText">暂无信息</p>
            </div>
          )

        let content2 = this.state.training ? (
            <div>
              <span style={{fontSize:'1.2em'}}>欢迎进入培训阶段</span> 
              <div className="messageCard">
                <div style={{background:'#0c3450',width:'84vw',height:'1.6vw'}}/>
                <List className="my-list">
                  <Item extra={this.state.training && this.state.training.date}>日期</Item>
                </List>

                <List className="my-list">
                  <Item extra={this.state.training && this.state.training.title}>课程名称</Item>
                </List>

                <List className="my-list">
                  <Item extra={this.state.training && this.state.training.teacher}>主讲人</Item>
                </List>

                <List className="my-list">
                  <Item extra={this.state.training && this.state.training.address}>地点</Item>
                </List>
              </div>
            </div>
          ) : (
            <div>
              <span style={{fontSize:'1.2em',lineHeight:'25px'}}>欢迎进入培训阶段</span> 
              <p className="nullText">暂无信息</p>
            </div>
          )
          
        let content3 = (
          <div>
            <span style={{fontSize:'1.2em',lineHeight:'25px'}}>{this.state.notice}</span>
            <Link to={`${_prefix}/resume/1`} 
            style={{fontSize:'1.2em', marginLeft: '5px', textDecoration:'underline', color:'blue'}}>
            重新申请</Link>
          </div>
          )

        if (this.state.ready) {
          return (
            <div>
                <Helmet><title>我是志愿者</title></Helmet>

                <p style={{background:'#0c3450',width:'100vw',height:'8.5vw',
                color:'#fff',fontSize:'1.2em',lineHeight:'8.5vw',textAlign:'center'}}>{this.state.userName}，欢迎你</p>

                <p style={{color:'rgba(255,255,255,0.5)',
                          fontSize:'1.2em',lineHeight:'8.5vw',
                          position:'absolute',top:0,right:'4vw'}}
                   onClick={this.tologout}
                >[退出登录]</p>

                <div 
                  style={{background:`url(${bg1})  center no-repeat / contain`,
                          width:'100vw',height:'32vw',marginTop:'5.6vw'}}>
                  {(this.state.step === 0) ? text0 : ((this.state.step === 1)? text1 : ((this.state.step === 2)? text2 : text3))}
                </div>
                
                <div 
                  style={{background:`url(${bg2}) bottom center no-repeat / contain`,
                  width:'100vw',height:'calc(100vh - 46.1vw)',padding:'9.5vw 4vw'
                  }}>
                  <div style={{width:'92vw',height:'100vw',
                  border:'1px solid #bfbfbf',padding:'8vw 4vw',
                  background:'rgba(255,255,255,0.6)'}}>
                    {(this.state.step === 0) ? content0 : ((this.state.step === 1)? content1 : ((this.state.step === 2)? content2 : content3))}
                  </div>
                </div>
            </div>
          );
        } else {
          return <div/>
        }
        
    }
}

export default withRouter(Application);




