import React, { Component } from 'react'
import { Flex, TextareaItem } from 'antd-mobile'
import './style.css'

class Page6 extends Component {

    render() {
        let { datas, setData } = this.props
        return (
            <div>
                <TextareaItem 
                    value = {datas.experience}
                    placeholder = '请输入您的学习及工作经历'
                    style = {{height:'138vw',margin:'4vw',width:'92vw',fontSize:'1.2em'}}
                    onChange = {v => setData('experience', v)}
                />

                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.toLastStep} className="bottomBtn">上一步</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.props.nextStep} className="bottomBtn confirm">确认提交</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page6;



