import React, { Component } from 'react';
import {Helmet} from "react-helmet";
import { Flex, Modal } from 'antd-mobile'
import { withRouter } from 'react-router'
import moment from 'moment';
import { uploadResume, getMyInfo, modifyResume } from '../../lib/api'
import Step1 from './Step1'
import Step2 from './Step2'
import Step3 from './Step3'
import Step4 from './Step4'
import Step5 from './Step5'
import Step6 from './Step6'
import './style.css'

// import bg1  from '../../assets/line.png';
// import bg2  from '../../assets/linePoint.png';
// import line from '../../assets/headline.png';
const bg1 = '/static/volunteer/line.png'
const bg2 = '/static/volunteer/linePoint.png'
const line = '/static/volunteer/headline.png'

const _prefix = window._config._prefix.link

let headers = ["基本信息一", "基本信息二", "志愿服务时间", "个人专业技能", "您想要从事的志愿服务岗位", "学习及工作经历（从高中填起）"]
class Application extends Component {

    constructor(props) {
      super(props);
      this.state = {
        add  : 0,
        step : 0,
        formDatas : {
            // -------step1------- 
            name        : '',               
            sex         : '',
            birthday    : moment().unix(),          
            education   : '',
            idCard      : '',              
            tel         : '',                 
            mail        : '',               
            photo       : '',              
            type        : '',                 
            unit        : '',

            // -------step2------- 
            homeAddress : '',
            mailAddress : '',
            zipCode     : '',            
            contact     : [],          

            // -------step3------- 
            serviceHours: [],

            // -------step4------- 
            skill       : [],

            // -------step5------- 
            jobs        : [],

            // -------step6------- 
            experience  : '',
        },
      };
    }   

    async componentWillMount(){
        let curUrl = this.props.match.params.add
        if(curUrl == 1){
            let res = await getMyInfo()
            if(res && res.code){
                let data = res.data
                this.setState({
                    add : curUrl,
                    formDatas : {
                        name        : data.name,               
                        sex         : data.sex,
                        birthday    : data.birthday,           
                        education   : data.education,
                        idCard      : data.idCard,          
                        tel         : data.tel,              
                        mail        : data.mail,               
                        photo       : data.photo,              
                        type        : data.type,                
                        unit        : data.unit,
                        homeAddress : data.homeAddress,
                        mailAddress : data.mailAddress,
                        zipCode     : data.zipCode,            
                        contact     : data.contact,           
                        serviceHours: data.serviceHours,
                        skill       : data.skill,
                        jobs        : data.jobs,
                        experience  : data.experience,
                    }
                });
            }
        }
    }

    _checkEmpty = (arr) =>{
        let notEmpty = true
        arr.map((item)=>{
            let content = this.state.formDatas[item]
            if( content === '' ) {
                notEmpty = false
            }else if (content.length === 0){
                notEmpty = false
            }
        })
        return notEmpty
    }

    _setEmpty = () =>{
        let arr = ['name', 'sex', 'birthday', 'education', 'idCard', 'tel', 'mail', 'type', 'profession']
        let datas = this.state.formDatas
        arr.map((item)=> datas[item] = '' )
        this.setState({
            formDatas : datas
        });
    }

    _setData = (key, value) =>{
        let datas = this.state.formDatas
        datas[key] = value
        this.setState({
            formDatas : datas 
        });
    }

    _nextStep = async () =>{
        if(this.state.step == 5){
            if(this.state.add){
                let ret = await modifyResume(this.state.formDatas)
                if(ret){
                    if(ret.code){
                        Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定', onPress: () => this.props.history.push(`${_prefix}/auditing`), style: 'default' }])
                    }else{
                        Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定',style: 'default' }])
                    }
                }else{
                    Modal.alert("提示", "服务器出现异常，稍后重试", [{ text: '确定',style: 'default' }])
                }
            }else{
                let ret = await uploadResume(this.state.formDatas)
                if(ret){
                    if(ret.code){
                        Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定', onPress: () => this.props.history.push(`${_prefix}/auditing`), style: 'default' }])
                    }else{
                        Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定',style: 'default' }])
                    }
                }else{
                    Modal.alert("提示", "服务器出现异常，稍后重试", [{ text: '确定',style: 'default' }])
                }
            }
        }else{
            this.setState({
                step : this.state.step + 1
            },this.swipeHalf);
        }
    }

    _LastStep = () =>{
        this.setState({
            step : this.state.step - 1
        },this.swipeHalf);
    }

    swipeHalf = () =>{
        this.header.style.left = 0
        let headerWidth = this.header.scrollWidth / 2
        let dotWidth    = this.dot.offsetWidth /2 
        this.header.style.left = `-${headerWidth - dotWidth}px`

        let rightWidth = document.body.clientWidth * 0.85 - (headerWidth - dotWidth) - (this.line.offsetWidth / 5) * this.state.step
        if(rightWidth < 0){
            this.header.style.left = `-${headerWidth - dotWidth - rightWidth}px`
        }

        let leftWidth  = document.body.clientWidth * 0.15 - (headerWidth + dotWidth) + (this.line.offsetWidth / 5) * this.state.step
        if(leftWidth < 0){
            this.header.style.left = `-${headerWidth - dotWidth + leftWidth}px`
        }
    }

    componentDidMount(){
        this.swipeHalf()
    }

    render() {
        let {formDatas, step, add} = this.state
        let curUrl = this.props.match.params.add

        let step1 = (step === 0) ? (
                <Step1 
                    status= {add}
                    datas = {formDatas}
                    setData = {this._setData}
                    setEmpty = {this._setEmpty}
                    checkEmpty = {this._checkEmpty}
                    nextStep={this._nextStep} 
                />
            ) : null

        let step2 = (step === 1) ? (
                <Step2 
                    datas= {formDatas}
                    setData = {this._setData}
                    nextStep={this._nextStep}
                    checkEmpty = {this._checkEmpty}
                    toLastStep={this._LastStep}
                />
            ) : null

        let step3 = (step === 2) ? (
                <Step3 
                    datas= {formDatas}
                    setData = {this._setData}
                    nextStep={this._nextStep}
                    checkEmpty = {this._checkEmpty}
                    toLastStep={this._LastStep}
                />
            ) : null

        let step4 = (step === 3) ? (
                <Step4
                    datas= {formDatas}
                    setData = {this._setData}
                    nextStep={this._nextStep}
                    checkEmpty = {this._checkEmpty}
                    toLastStep={this._LastStep}
                />
            ) : null

        let step5 = (step === 4) ? (
                <Step5
                    datas= {formDatas}
                    setData = {this._setData}
                    nextStep={this._nextStep}
                    checkEmpty = {this._checkEmpty}
                    toLastStep={this._LastStep}
                />
            ) : null

        let step6 = (step === 5) ? (
                <Step6
                    datas= {formDatas}
                    setData = {this._setData}
                    nextStep={this._nextStep}
                    checkEmpty = {this._checkEmpty}
                    toLastStep={this._LastStep}
                />
            ) : null


        let ready =  curUrl == 1  &&  formDatas.name == '' 
        
        return (
                <div style={{background:'#fff',height:'100vh'}}>
                    <Helmet><title>申请加入志愿者</title></Helmet>

                    <Flex direction='column' style={{width:'100vw'}}>
                        <div src={bg1} 
                            style={{width:'70vw',height:'4.8vw',
                                    marginTop:'6vw',position:'relative',
                                    background: `url(${bg1})  center no-repeat / contain`
                                }}
                            ref = {(line) => this.line = line}
                        >
                            <div 
                                style={{width:'4.8vw',height:'4.8vw',transition: 'left 1s',
                                background: `url(${bg2})  center no-repeat / contain`, position:'relative',
                                transform:'translateX(calc(-1.8vw + 13.7vw *'+step+'))'
                                }}
                                ref = {(dot) => this.dot = dot}
                            >
                                <span 
                                    style={{position:'absolute',top:'6.8vw',
                                    wordBreak:'keep-all',whiteSpace:'nowrap',
                                    fontSize:'1.45em',color:'#333333'}}
                                    ref = {(header) => this.header = header}
                                >{headers[step]}</span>
                            </div>
                        </div>
                        
                        <img src={line} style={{width:'92vw',height:'4vw',margin:'12vw 0 2vw'}}  alt="正在加载中"/>
                    </Flex>

                    {
                        ready ? '' : (
                            <div style={{height:'calc(100vh-100vw)'}} className='page'>
                                {
                                    step1
                                }
                                {
                                    step2
                                }
                                {
                                    step3
                                }
                                {
                                    step4
                                }
                                {
                                    step5
                                }
                                {
                                    step6
                                }
                            </div>
                        )
                    }
                    
                </div>
        );        
    }
}

export default withRouter(Application);



