import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Flex } from 'antd-mobile'
import { Route } from 'react-router-dom';
import { Helmet } from "react-helmet";
import RouteWithSubRoutes from '../../router/RouteWithSubRoutes'
import { getMyInfo, logout } from '../../lib/api'
import { withRouter } from 'react-router'
import './style.css'

// import bg1  from '../../assets/card.png';
// import bg2  from '../../assets/hua.png';
const bg1 = '/static/volunteer/card.png'
const bg2 = '/static/volunteer/hua.png'


const _prefix = window._config._prefix.link

class Daily extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
            userName: '',
            level: ''
        };
    }

    async componentWillMount(){
        let ret = await getMyInfo()
        if (ret && ret.code) {
            let curStep 
            switch(ret.data.level) {
                case 'trainee':
                    curStep = '见习'
                    break;
                case 'junior':
                    curStep = '初级'
                    break;
                case 'advanced':
                    curStep = '高级'
                    break;
                default:
                    curStep = '资深'
            }

            this.setState({
                userName: ret.data.name,
                level: curStep
            });
        }
    }

    tologout = async () => {
        let ret = await logout()
        if (ret && ret.code === true) {
            this.props.history.replace(`${_prefix}/volunteer`)
        }
    }
    
    render() {
        return (
            <div>
                <Helmet><title>我是志愿者</title></Helmet>

                <p style={{background:'#0c3450',width:'100vw',height:'8.5vw',
                color:'#fff',fontSize:'1.2em',lineHeight:'8.5vw',textAlign:'center'}}>{this.state.userName}，欢迎你</p>

                <p style={{color:'rgba(255,255,255,0.5)',
                          fontSize:'1.2em',lineHeight:'8.5vw',
                          position:'absolute',top:0,right:'4vw'}}
                   onClick={this.tologout}
                >[退出登录]</p>

                <Flex justify='center'
                  style={{background:`url(${bg1})  center no-repeat / contain`,
                          width:'10.5vw', height:'30.5vw',
                          position:'absolute', top:'3vw',left:'4vw'}}>
                  <span className="textContent">{this.state.level}志愿者</span>
                </Flex>
                
                <div style={{background:`url(${bg2}) bottom center no-repeat / contain`,height:'calc(100vh - 8.5vw)'}}>

                <Route exact path={`${_prefix}/daily`}
                  render={()=>{return (
                    <Flex direction='column'
                      style={{width:'100vw',height:'100%'}}>

                      <Link to={`${_prefix}/daily/appoint`}>
                        <Flex className="btnStyle" justify='center'
                          style={{marginTop:'31vw'}} 
                        >服务预约</Flex>
                      </Link>

                      <Link to={`${_prefix}/daily/record`}>
                        <Flex className="btnStyle" justify='center'>
                          预约记录
                        </Flex>
                      </Link>
                      
                      <Link to={`${_prefix}/daily/history`}>
                        <Flex className="btnStyle" justify='center'>
                          历史服务记录
                        </Flex>
                      </Link>

                      <Link to={`${_prefix}/resume/1`}>
                        <Flex className="btnStyle" justify='center'>
                          修改资料
                        </Flex>
                      </Link>
                    </Flex>
                  )}}/>
                {
                  this.props.routes.map((route, i) => (
                    <RouteWithSubRoutes key={i} {...route}/>
                  ))
                }
                </div>
            </div>
        );
    }
}

export default withRouter(Daily);




