import 'whatwg-fetch';
let donate = window._config._prefix.api;
let upload = window._config._prefix.upload;
const _prefix = window._config._prefix.link

const getData = (args)=>{
    let data = new FormData();
    for(let i in args){
        data.append(i, args[i]);
    }
    return data;
}
const fetchFile = (url,args) => {
    let data = getData(args);
    return fetch(url,{
        method: 'post',
        mode: 'cors',
        credentials: 'include',
        body: data
    }).then(async (data) => {
        let res = await data.json();
        if(res.code===403 || data.status===401){
            window.location.href=`${_prefix}/volunteer`;
        }
        return res;
    });
}

const fetchJson = (url,args)=>{
    return fetch(url,{
        method: 'post',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        mode: 'cors',
        credentials: 'include',
        body: JSON.stringify(args)
    }).then(async (data) => {
        if((data.status>=200) && (data.status<300)){
            let res = await data.json();
            // console.log('res: ',res)
            return res
        }else if(data.status>300){
            if(data.status===403 || data.status===401){
                window.location.href=`${_prefix}/volunteer`;
            }
            return {
                code: false,
                msg: '检查标识是否重复，可不填写;或者联系网络管理员'
            }
        }
    });
}


// 登录
export const login = (args={}) => {
    return fetchJson(`${donate}/volunteer/login`,args);
}
// 退出
export const logout = (args={}) => {
    return fetchJson(`${donate}/volunteer/logout`,args);
}
