import Home         from '../components/Home'
import Article      from '../components/Article'
import TeamIntro    from '../components/TeamIntro'

import NewsList     from '../components/News/index'
import Volunteer    from '../components/Login/index'

import Application  from '../components/Notice/index'
import ApplingProc  from '../components/Apply/index'
import AuditingProc from '../components/Audit/index'

import VolDaily     from '../components/Daily/index'
import VolRecord    from '../components/Daily/record'
import VolAppoint   from '../components/Daily/appointment'
import VolHistory   from '../components/Daily/history'

const prefix = window._config._prefix.link

const routes = [
    {
        path: `${prefix}/home`,
        component: Home,
    },
    {
        path: `${prefix}/team`,
        component: TeamIntro,
    },
    {
        path: `${prefix}/news`,
        component: NewsList,
    },
    {
        path: `${prefix}/article/:id`,
        component: Article,
    },
    {
        path: `${prefix}/apply`,
        component: Application,
    },
    {
        path: `${prefix}/volunteer`,
        component: Volunteer,
    },
    // {
    //     path: `${prefix}/applying`,
    //     component: ApplingProc,
    // },
    {
        path: `${prefix}/resume/:add`,
        component: ApplingProc,
    },
    {
        path: `${prefix}/auditing`,
        component: AuditingProc,
    },
    {
        path: `${prefix}/daily`,
        component: VolDaily,
        routes: [
            {
                path: `${prefix}/daily/record/`,
                component: VolRecord
            },
            {
                path: `${prefix}/daily/appoint/`,
                component: VolAppoint
            },
            {
                path: `${prefix}/daily/History/`,
                component: VolHistory
            },
        ]
    },
    
    
]
export default routes;


