import React, { Component } from 'react';
import { Flex, List, InputItem } from 'antd-mobile'
import './style.css'

class Page2 extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
            err1: false,
            err2: false,
            err3: false,
            datas:{},
            homeAddress: '',
            mailAddress: '',

        };
    } 

    toNextStep = () =>{
        let err = true
        let { datas, checkEmpty } = this.props
        if(datas.zipCode.length < 6){
            err = false
            this.setState({ err1 : true});
        }

        if(datas.contact[0]){
            if(!datas.contact[0].name || !datas.contact[0].tel){ err = false } 
            if(datas.contact[0].tel && datas.contact[0].tel.length < 6){ 
                err = false 
                this.setState({ err2 : true});
            }
        }else{
            err = false 
        }

        if(datas.contact[1]){
            if(!datas.contact[1].name || !datas.contact[1].tel){ err =false } 
            if(datas.contact[1].tel && datas.contact[1].tel.length < 6){ 
                err = false 
                this.setState({ err3 : true});
            }
        }else{
            err = false 
        }

        let i = checkEmpty(['homeAddress', 'mailAddress', 'zipCode'])

        if(i && err){
            this.props.nextStep()
        }else{
            alert('请填写本页面每一项信息')
        }
    }

    render() {
        let { datas, setData } = this.props
        return (
            <div>
                <List style={{margin:'0 4vw'}} className="step2">
                    <InputItem
                        clear={true}
                        value={datas.homeAddress}
                        onChange={v => setData('homeAddress', v)}
                    >家庭住址</InputItem>
                      
                    <InputItem
                        clear={true}
                        value={datas.mailAddress}
                        onChange={v => setData('mailAddress', v)}
                    >通讯地址</InputItem>
                    
                    <InputItem
                        maxLength={6}
                        type='number'
                        clear={true}
                        error={this.state.err1}
                        value={datas.zipCode}
                        onChange={v => {
                            setData('zipCode', v )
                            if(v.length == 6){
                                this.setState({ err1 : false });
                            }
                        }}
                    >邮编</InputItem>

                    <InputItem
                        maxLength={5}
                        clear={true}
                        value={datas.contact[0] && datas.contact[0].name}
                        onChange={v => {
                            let arr = datas.contact
                            arr[0] = arr[0] ? arr[0] : {name: ''}
                            arr[0].name = v
                            setData('contact', arr)
                        }}
                    >紧急联系人1</InputItem>

                    <InputItem
                        type='phone'
                        clear={true}
                        error={this.state.err2}
                        value={datas.contact[0] && datas.contact[0].tel}
                        onChange={v => {
                            let arr = datas.contact
                            arr[0] = arr[0] ? arr[0] : {tel: ''}
                            arr[0].tel = v
                            setData('contact', arr)
                            if(v.length == 11){
                                this.setState({ err2 : false });
                            }
                        }}
                    >联系电话</InputItem>

                    <InputItem
                        maxLength={5}
                        clear={true}
                        value={datas.contact[1] && datas.contact[1].name}
                        onChange={v => {
                            let arr = datas.contact
                            arr[1] = arr[1] ? arr[1] : {name: ''}
                            arr[1].name = v
                            setData('contact', arr)
                        }}
                    >紧急联系人2</InputItem>

                    <InputItem
                        type='phone'
                        clear={true}
                        error={this.state.err3}
                        value={datas.contact[1] && datas.contact[1].tel}
                        onChange={v => {
                            let arr = datas.contact
                            arr[1] = arr[1] ? arr[1] : {tel: ''}
                            arr[1].tel = v
                            setData('contact', arr)
                            if(v.length == 11){
                                this.setState({ err3 : false });
                            }
                        }}
                    >联系电话</InputItem>

                </List>
                

                
                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.toLastStep}  className="bottomBtn">上一步</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.toNextStep}  className="bottomBtn">下一步</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page2;



