import React, { Component } from 'react';
import { Flex, List, Checkbox, Modal} from 'antd-mobile'
import './style.css'
const CheckboxItem = Checkbox.CheckboxItem;

const data = [
          { value: 0, label: '讲解' },
          { value: 1, label: '观众问卷调查' },
          { value: 2, label: '活动策划' },
          { value: 3, label: '海报、视频制作' },
          { value: 4, label: '文案' },
          { value: 5, label: '档案整理' },
          { value: 6, label: '观众引导' },
        ];


class Page5 extends Component {

    toNextStep = () =>{
        let i = this.props.checkEmpty(['jobs'])
        if(i){
            this.props.nextStep()
        }else{
            Modal.alert("提示", "请至少选择一项服务内容", [{ text: '确定', style: 'default' }])
        }
    }
    
    containItem = (arr, item) =>{
        let hasItem = false
        arr.map(i=>{
            if(i.key == item )
                hasItem = true
        })
        return hasItem
    }

    removeItem = (arr,item) =>{
        for(let i=0; i<arr.length; i++) {
            if(arr[i].key == item) {
              arr.splice(i, 1);
              break;
            }
        }
    }

    checkItem0 = (value)=>{
        let arr = this.props.datas.jobs
        if(!this.containItem(arr, value)){
            arr.push({key: value})
        }else{
            this.removeItem(arr, value)
        }
        this.props.setData('jobs', arr)
    }

    render() {
        let { datas } = this.props
        let { jobs } = datas
        let arr = []
        jobs.map(item =>{
            arr.push(item.key)
        })
        return (
            <div className="page4">
                <List className="step5">
                    {data.map((i, index) => (
                      <CheckboxItem key={i.value}
                      checked={arr.indexOf(index)>=0}
                      onChange={() => this.checkItem0(i.value)}>
                          {i.label}
                      </CheckboxItem>
                    ))}
                </List>

                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.toLastStep} className="bottomBtn">上一步</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.toNextStep} className="bottomBtn">下一步</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page5;



