import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";
import { getArticles } from '../lib/api'

// import footer from '../assets/footer.png';
const footer = '/static/volunteer/footer.png'

const _prefix = window._config._prefix.link
const _name = window._config.names.teamIntro

class IntroList extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        ret1: null
      };
    }

    makeRemoteRequest = async () =>{
        let ret1 = await getArticles({category:{name: _name}, page:1, limit:3})
        this.setState({
            ret1: ret1 && ret1.data
        });
    } 

    componentWillMount(){
        this.makeRemoteRequest()
    }

    render() {
        return (
            <div>
                <Helmet><title>志愿者团队介绍</title></Helmet>

                {
                  this.state.ret1 && this.state.ret1.map((item,index)=>{
                    return(
                      <IntroItem item={item} key={index}/>
                    )
                  })
                }

                <div style={{padding:'5.2vw 0 4vw 0', textAlign:'center'}}>
                	<img src={footer} style={{width:'60vw',height:'4vw'}} alt="正在加载中"/>
                </div>
            </div>
        );
    }
}

class IntroItem extends Component {
    render() {
      let {item} = this.props
        return (
          <Link to={`${_prefix}/article/${item._id}`} >
            <div style={{
              width:'92vw', height:'40vh', backgroundColor: '#fff',
              padding:'4vw 2.6vw', margin:' 4vw 4vw 0 4vw'}}>
               	<div style={{height:'4vw',width:'100%',marginBottom:'4vw'}}>
               		<p style={{fontSize:'1.45em', lineHeight:'1.45em',width:'100%'}}
                      className="textTitle">{item.title}</p>
               	</div>
               	<img src={item.cover} style={{width:'86.8vw',height:'30vh'}} alt="正在加载中"/>
            </div>
          </Link>
        );
    }
}


export default IntroList;



