import React, { Component } from 'react';
import { Flex, List, InputItem, Picker, DatePicker, Modal } from 'antd-mobile'
import './style.css'
import { uploadImg } from '../../lib/api'
import moment from 'moment';

// import bg1  from '../../assets/avatar.png';
const bg1 = '/static/volunteer/avatar.png'

const gender    = [{"label":"男", value:0}, {"label":"女", value:1}]
const education = [{"label":"小学", value:0}, {"label":"初中", value:1}, {"label":"高中", value:2}, {"label":"大学及以上", value:3}]
const vtype     = [{label:'小小讲解员', value:0}, {label:'大学生志愿者', value:1}, {label:'社会志愿者', value:2}]

const emailReg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$")

class Page1 extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        err1: false,
        err2: false,
        err3: false,
        text: '学校专业'
      };
    } 

    toNextStep = () =>{
        let { datas, checkEmpty } = this.props
        let err = true
        if(datas.idCard.length < 18){
            err =false
            this.setState({ err1 : true },()=>console.log('出错了'));
        }
        if(datas.tel.length < 11){
            err =false
            this.setState({ err2 : true });
        }
        if(!emailReg.test(datas.mail)){
            err =false
            this.setState({ err3 : true });
        }

        let i = checkEmpty(['name', 'sex', 'birthday', 'education', 'idCard', 'tel', 'mail', 'type', 'unit', 'photo'])

        if(i && err){
            this.props.nextStep()
        }else{
            Modal.alert("提示", "请填写本页面每一项信息", [{ text: '确定', style: 'default' }])
        }
    }

    _uploadImg = async (e)=>{
        let ret1 = await uploadImg({file: e.target.files[0]})
        if(ret1){
            this.props.setData('photo',ret1.url)
        }
    }

    // componentDidMount(){
    //     var x=document.getElementById("test");
    //     console.log(x)

    //     var y=document.getElementsByClassName("bottomBtn");
    //     console.log(y,y[0])

    //     console.log(this.btn)
    // }

    render(){
        let { datas, setData, status } = this.props
        return (
            <div>
                <List style={{margin:'0 4vw 14vw'}}  className="step1">
                    <Flex>
                        <InputItem 
                            maxLength={5}
                            clear={true}
                            disabled={status}
                            value={datas.name}
                            onChange={v => setData('name', v)}
                        >姓名</InputItem>

                        <div style={{flex:1}}>
                            <Picker 
                                data={gender}  cols={1} 
                                value={[datas.sex]}
                                onOk={v =>  setData('sex', v[0]) }
                            >
                              <List.Item arrow="horizontal">性别</List.Item>
                            </Picker>
                        </div>
                    </Flex>

                    <Flex>
                        <div className="flexDivDate">
                            <DatePicker
                              mode="date"
                              extra="请选择"
                              minDate={moment('1900-01-01').toDate()}
                              maxDate={moment().toDate()}
                              value={moment.unix(datas.birthday).toDate() }
                              onChange={v => { setData('birthday', moment(v).unix()) }}
                            >
                              <List.Item arrow="horizontal">出生年月</List.Item>
                            </DatePicker>
                        </div>

                        <div style={{flex:1}}>
                            <Picker 
                                cols={1} data={education} 
                                value={[datas.education]}
                                onOk={v => setData('education', v[0])}
                            >
                              <List.Item arrow="horizontal">学历</List.Item>
                            </Picker>
                        </div>
                    </Flex>

                    <InputItem
                        type='number'
                        maxLength={18}
                        clear={true}
                        disabled={status}
                        value={datas.idCard}
                        error={this.state.err1}
                        onChange={v => {
                            setData('idCard', v)
                            if(v.length == 18){
                                this.setState({ err1 : false });
                            }
                        }}
                    >身份证号</InputItem>
                      
                    <InputItem
                        type='phone'
                        clear={true}
                        value={datas.tel}
                        error={this.state.err2}
                        onChange={v => {
                            setData('tel', v)
                            if(v.length == 11){
                                this.setState({ err2 : false });
                            }
                        }}
                    >联系电话</InputItem>
                    
                    <InputItem
                        clear={true}
                        value={datas.mail}
                        error={this.state.err3}
                        onChange={v => {
                            setData('mail', v)
                            if(emailReg.test(datas.mail)){
                                this.setState({ err3 : false });
                            }
                        }}
                    >电子邮箱</InputItem>

                    <Flex style={{height:'33.6vw'}} justify='between'>
                        <span style={{fontSize:'1.2em'}}>照片上传</span>
                        
                        <button 
                            onClick={()=> { this.input.click() }}
                            style={{height:'26vw',width:'26vw',border:'1px #a0a0a0 solid',
                            background:`url(${datas.photo ? datas.photo : bg1}) center no-repeat / cover`}} />                        
                    </Flex>

                    <input type='file' accept='image/*' 
                        ref={refs => this.input = refs}
                        onChange={this._uploadImg}
                        style={{display:'none'}}/>

                    <div style={{background:'#ddd',width:'100%',height:'1px', opacity:'0.8'}} />

                    <div className="flexDiv">
                        <Picker 
                            data={vtype} cols={1} 
                            value={[datas.type]}
                            onOk={v => {
                                setData('type', v[0])
                                if(v[0] == 0){
                                    this.setState({ text : '学校班级' });
                                }else if(v[0] == 2){
                                    this.setState({ text : '工作单位' });
                                }else{
                                    this.setState({ text : '学校专业' });
                                }
                            }}
                        >
                            <List.Item arrow="horizontal">志愿者类别</List.Item>
                        </Picker>
                    </div>

                    <InputItem
                        clear={true} 
                        className='lastItem'
                        value={datas.unit}
                        onChange={v => setData('unit', v)}
                    >{this.state.text}</InputItem>

                </List>

                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.setEmpty} 
                            className="bottomBtn" 
                            id='test'
                            ref={(button) => { this.btn = button; }}
                        >重新填写</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.toNextStep} className="bottomBtn">下一步</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page1;





