import React, { Component } from 'react';
import { Flex, List, Checkbox, Modal } from 'antd-mobile'
import './style.css'
const CheckboxItem = Checkbox.CheckboxItem;

const data = [
          { value: 1, label: '周二' },
          { value: 2, label: '周三' },
          { value: 3, label: '周四' },
          { value: 4, label: '周五' },
          { value: 5, label: '周六' },
          { value: 6, label: '周日' },
        ];

class Page3 extends Component {

    toNextStep = () =>{
        let i = this.props.checkEmpty(['serviceHours'])
        if(i){
            this.props.nextStep()
        }else{
            Modal.alert("提示", "请至少选择一项服务时间", [{ text: '确定', style: 'default' }])
        }
    }

    render() {
        let { datas, setData } = this.props
        let { serviceHours } = datas
        return (
            <div>
                <p style={{fontSize:'1.2em',color:'#333333', margin:'2vw 4vw 4vw', lineHeight:'1.7em'
                }}>请选择您可支配的时间段（可多选），我们将根据需要选择其中半天为您固定服务时间。</p>

                <List style={{margin:'0 4vw'}} className="step3">
                    <div style={{background:'#ddd',width:'100%',height:'1px', opacity:'0.8'}} />

                {   
                    data.map((item,index) =>{
                        return(
                            <Flex key={item.value}>
                                <span style={{
                                    height:'43px', lineHeight:'43px',
                                    fontSize:'1.2em', paddingRight:'10px',
                                    borderRight:'1px #ddd solid',
                                    borderBottom:'1px #ddd solid',
                                }}>{item.label}</span>

                                <CheckboxItem 
                                    checked={serviceHours[index] && serviceHours[index].period.indexOf(0) >= 0}
                                    onChange={() => {
                                        let arr = serviceHours
                                        arr[index] = arr[index] ? arr[index] : {key: index+1, period:[]}
                                        
                                        if(arr[index].period.length == 0) {
                                            arr[index].period = [0]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                        if(arr[index].period.length == 1 && arr[index].period[0] == 0) {
                                            serviceHours[index] = undefined
                                            setData('serviceHours', arr)
                                            return
                                        } 
                                        if(arr[index].period.length == 1 && arr[index].period[0] == 1) {
                                            arr[index].period = [0,1]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                        if(arr[index].period.length == 2) {
                                            arr[index].period = [1]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                    }}
                                >
                                    上午
                                </CheckboxItem>

                                <CheckboxItem 
                                    checked={serviceHours[index] && serviceHours[index].period.indexOf(1) >= 0}
                                    onChange={() => {
                                        let arr = serviceHours
                                        arr[index] = arr[index] ? arr[index] : {key: index+1, period:[]}
                                        if(arr[index].period.length == 0) {
                                            arr[index].period = [1]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                        if(arr[index].period.length == 1 && arr[index].period[0] == 1) {
                                            serviceHours[index] = undefined
                                            setData('serviceHours', arr)
                                            return
                                        } 
                                        if(arr[index].period.length == 1 && arr[index].period[0] == 0) {
                                            arr[index].period = [0,1]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                        if(arr[index].period.length == 2) {
                                            arr[index].period = [0]
                                            setData('serviceHours', arr)
                                            return
                                        }
                                    }}
                                >
                                    下午
                                </CheckboxItem>
                            </Flex>
                        )
                    })
                }
                </List>

                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.toLastStep} className="bottomBtn">上一步</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.toNextStep} className="bottomBtn">下一步</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page3;



