import React, { Component } from 'react'
import moment from 'moment'
import { Flex, Button, Modal, TextareaItem } from 'antd-mobile'
import { getRecord, signActivity, cancelBooked } from '../../lib/api'

import './style.css'

export default class Record extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        showModal0: false,
        showModal1: false,
        cancel: false,
        ret: null,
        note: '',
        title: '',
        id: ''
      };
    }

    componentWillMount(){
        this.getRecord()
    }

    getRecord = async () =>{
        let res = await getRecord({pass: 0})
        this.setState({ ret: res.data })
    }

    // 开始前10分钟，结束后的半小时，签到签退
    cancelBtn = ()=>{
        this.setState({ cancel : !this.state.cancel })
    }

    _cancelItem = async (index, id)=>{
        let arr = this.state.ret
        let res = await cancelBooked({_id: id})
        if (res && res.code) {
            arr.splice(index, 1)
        }
        arr = (arr.length === 0) ? null : arr
        this.setState({ ret : arr })
    }

    _sign = async ()=>{
        if(this.state.id){
            let res = await signActivity({_id: this.state.id, note: this.state.note})
            if (res) {
                if (res.code) {
                    this.getRecord()
                    this._closeModal()
                } else {
                    Modal.alert("提示", <div>{res.msg}</div>, [{ text: '确定', style: 'default' }])
                }
            } else {
                Modal.alert("提示", <div>请求失败!</div>, [{ text: '确定', style: 'default' }])
            }
        }
    }

    _showModal0  = ()=>{ this.setState({ showModal0: true }); }

    _showModal1  = ()=>{ this.setState({ showModal1: true }); }

    _closeModal  = ()=>{ this.setState({ showModal0: false, showModal1: false }); }

    render() {
        let {ret, cancel, title} = this.state
        if (!ret) {
            return (
                <div className="fullBg">
                    <p style={{fontSize: '1.4em'}}>暂无预约记录</p>
                </div>
            )
        } else {
            return (
                <Flex direction='column' className='record'>
                    <Button onClick={this.cancelBtn}>{cancel ? '退出选择' : '取消预约'}</Button>
                    <p style={{fontSize:'1.45em',marginTop:'2vw'}}>预约记录</p>

                    <div style={{marginTop:'12vw'}} className="tableLine" />

                    <Flex className="tableBg" direction='column'>
                      <Flex>
                        <div className="tabledItem" 
                        style={{width:'32.8vw',height:'11vw'}}><span className="dayText">时间</span></div>
                        <div className="tabledItem" 
                        style={{width:'38.2vw',height:'11vw'}}><span className="dayText">岗位</span></div>
                        <div className="tabledItem" 
                        style={{width:'21vw',height:'11vw'}}><span className="dayText">考勤审核</span></div>
                      
                      </Flex>
                        {
                            this.state.ret.map((item,index)=>{
                                return(
                                    <RecordItem 
                                        key ={index}
                                        type={cancel}
                                        sign={item.sign}
                                        item={item.service}
                                        clickShow0={this._showModal0} 
                                        clickShow1={this._showModal1} 
                                        setId     ={() => this.setState({ id: item._id})}
                                        setTitle  ={() => this.setState({ title: item.service.title})}
                                        cancelItem={() => this._cancelItem(index, item._id)}
                                    />
                                )
                            })
                        }
                    </Flex>

                    <Modal
                      visible={this.state.showModal0}
                      transparent={true}
                      onClose={this._closeModal0}
                    >
                      <div className="modalContent">
                          <span className="modalTitle">签到</span>

                          <Flex className="modalList" justify='between' style={{marginTop:'9vw'}}>
                            <span className="modalText0">时间</span>
                            <span className="modalText" >{moment().format('YYYY-MM-DD  HH:mm')}</span>
                          </Flex>

                          <Flex className="modalList" justify='between' >
                            <span className="modalText0">岗位</span>
                            <span className="modalText" >{title}</span>
                          </Flex>

                          <Button 
                            className="modalBtn"
                            onClick={this._sign}>签到</Button>
                      </div>
                    </Modal>

                    <Modal
                      visible={this.state.showModal1}
                      transparent={true}
                      onClose={this._closeModal1}
                    >
                      <div className="modalContent">
                          
                          <span className="modalTitle">签退</span>

                          <Flex className="modalList" justify='between' style={{marginTop:'9vw'}}>
                            <span className="modalText0">时间</span>
                            <span className="modalText" >{moment().format('YYYY-MM-DD  hh:mm')}</span>
                          </Flex>

                          <Flex className="modalList" justify='between' >
                            <span className="modalText0">岗位</span>
                            <span className="modalText" >{title}</span>
                          </Flex>
                          
                          <TextareaItem
                              title="备注"
                              placeholder="请记录服务人数或讲解场次人数"
                              autoHeight={true}
                              onChange={e => {
                                this.setState({ note: e })
                              }}
                          />
                          
                          <div style={{borderBottom: '1px solid #d3d3d3'}} />
                          <Button 
                            className="modalBtn"
                            onClick={this._sign}>签退</Button>
                        </div>
                    </Modal>
                </Flex>
            )
        }
        
    }
}

class RecordItem extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      signedIn : (this.props.sign && this.props.sign.in) ? true : false,
      signedOut: (this.props.sign && this.props.sign.back) ? true : false,
      startTime: this.props.item.time.start,
      endTime: this.props.item.time.end,
    };
  }

  componentWillReceiveProps(nextProps){
    this.setState({
        signedIn : (nextProps.sign && nextProps.sign.in) ? true : false,
        signedOut: (nextProps.sign && nextProps.sign.back) ? true : false,
    });
  }

  _toSignIn = () =>{
    // 到活动时间
    let today = moment().valueOf()
    if(today < (this.state.startTime - 600000)){
      Modal.alert("提示", <div>未到活动时间!</div>, [{ text: '确定', style: 'default' }])
    }else if(today > (this.state.endTime)){
      Modal.alert("提示", <div>活动时间已过!</div>, [{ text: '确定', style: 'default' }])
    }else{
        this.props.setId()
        this.props.setTitle()
        this.props.clickShow0()
    }
  }

  _toSignOut = () =>{
    let today = moment().valueOf()
    if(this.state.signedIn){
      if(today < (this.state.endTime+1800000)){
        if(today < this.state.endTime){
            Modal.alert("提示", <div>活动尚未结束!</div>, [{ text: '确定', style: 'default' }])
        }else{
            this.props.setId()
            this.props.setTitle()
            this.props.clickShow1()
        }
              }else{
        Modal.alert("提示", <div>活动时间已过!</div>, [{ text: '确定', style: 'default' }])
      }
    }else {
      Modal.alert("提示", <div>尚未签到!</div>, [{ text: '确定', style: 'default' }])
    }
  }

  render(){
    let today = moment().valueOf()
    let typeBtn = (this.props.type && !this.state.signedIn && (today < this.state.startTime)) ? (
        <span className="cancelBtn"  onClick={this.props.cancelItem}>取消预约</span>
      ) : (
        <span className="dayText">待审核</span>
      )

    let signInBtn = (this.state.signedIn) ? (
        <Button style={{background:'#8ba6b9'}}>已签到</Button> 
      ) : (
        <Button onClick={this._toSignIn}>签到</Button> 
      )

    let signOutBtn = (this.state.signedOut) ? (
        <Button style={{background:'#8ba6b9'}}>已签退</Button>
      ) : (
        <Button onClick={this._toSignOut}>签退</Button>
      )

    let {startTime, endTime} = this.state
    let time0 = moment(startTime).format("YYYY-MM-DD")
    let time1 = moment(startTime).format("HH:mm")
    let time2 = moment(endTime).format("HH:mm")
    let time3 = time1 + '-' + time2

    return(
      <Flex>
        <Flex 
          direction='column' justify='center'
          className="tabledItem" 
          style={{width:'32.8vw',height:'20vw'}}>
            <span style={{fontSize:'1.2em',lineHeight:'1.6em'}}>{time0}</span>
            <span style={{fontSize:'1.2em',lineHeight:'1.6em'}}>{time3}</span>
        </Flex>
        
        <Flex 
          direction='column' 
          className="tabledItem" 
          style={{width:'38.2vw',height:'20vw'}}>
            <span className="dayText">{this.props.item.title}</span>
            <Flex justify='around' style={{width:'90%'}}>
              {signInBtn}
              {signOutBtn}
            </Flex>
        </Flex>
                    
        <Flex 
          className="tabledItem" justify='center'
          style={{width:'21vw',height:'20vw'}}>
            {typeBtn}
        </Flex>
      </Flex>
    )
  }
}


