import React, { Component } from 'react';
import { Flex, Modal, Button } from 'antd-mobile'
import { getRecord, getMyInfo } from '../../lib/api'
import moment from 'moment'
import './style.css'

// import bg1  from '../../assets/cha.png';
// import bg2  from '../../assets/bg0.png';
// import bg3  from '../../assets/bg1.png';
const bg1 = '/static/volunteer/cha.png'
const bg2 = '/static/volunteer/bg0.png'
const bg3 = '/static/volunteer/bg1.png'

export default class History extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        ret: [],
        name: '',
        curData: null,
        showAll: false,
        showModal : false,
      };
    }

    async componentWillMount(){
        let res0 = await getRecord({pass: [1, -1]})
        let res1 = await getMyInfo()
        this.setState({
          ret : res0.data,
          name: res1.data.name
        })
    }

    _showModal = (index)=>{
        this.setState({
            curData: this.state.ret[index],
            showModal: true
        });
    }

    _closeModal = ()=>{
      this.setState({
        showModal: false
      });
    }

    _showAll = () => {
        let state = this.state.showAll
        this.setState({
            showAll: !state
        });
    }

    render() {
        let {ret, curData, showAll, name} = this.state
        let time0 = curData && moment(curData.service.time.start).format("YYYY-MM-DD HH:mm")
        let time1 = curData && moment(curData.service.time.end).format("HH:mm")
        let time2 = curData && curData.sign && moment(curData.sign.in).format("YYYY-MM-DD HH:mm")
        let time3 = curData && curData.sign && moment(curData.sign.back).format("YYYY-MM-DD HH:mm")

        if (ret) {
            return (
                <Flex direction='column' className='record'>
                    <Button onClick={this._showAll}>{showAll ? '显示通过' : '显示全部'}</Button>

                    <p style={{fontSize:'1.45em',margin:'2vw'}}>历史服务记录</p>

                    <p style={{fontSize:'1em',marginTop:'2vw'}}>您已服务2次，共4小时</p>

                    <div style={{marginTop:'12vw'}} className="tableLine" />

                    <Flex className="tableBg" direction='column'>
                      <Flex>
                        <div className="tabledItem" 
                        style={{width:'32.8vw',height:'11vw'}}><span className="dayText">时间</span></div>
                        <div className="tabledItem" 
                        style={{width:'38.2vw',height:'11vw'}}><span className="dayText">岗位</span></div>
                        <div className="tabledItem" 
                        style={{width:'21vw',height:'11vw'}}><span className="dayText">考勤审核</span></div>
                      </Flex>
                      {
                        ret.map((item, index) => {
                            if(item.pass===-1 && !this.state.showAll){
                                return null
                            }else{
                                return(
                                    <HistoryItem 
                                        key={index} 
                                        pass={item.pass}
                                        content={item.service} 
                                        clickShow={() => this._showModal(index)} 
                                    />
                                )
                            }
                        })
                      }
                    </Flex>

                    <Modal
                      visible={this.state.showModal}
                      transparent={true}
                      className='historyModal'
                      maskClosable={false}
                    >
                      <div>
                        <div style={{height:'87.6vw', overflow: 'scroll', background:'#fff', padding:'4vw' }}>
                          
                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">时间</span>
                            <span className="modalText" >{time0}-{time1}</span>
                          </Flex>

                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">志愿者姓名</span>
                            <span className="modalText" >{name}</span>
                          </Flex>

                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">服务内容</span>
                            <span className="modalText" >{curData && curData.service.title}</span>
                          </Flex>

                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">签到</span>
                            <span className="modalText" >{time2}</span>
                          </Flex>

                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">签退</span>
                            <span className="modalText" >{time3}</span>
                          </Flex>

                          <Flex className="modalList" justify='between'>
                            <span className="modalText0">备注</span>
                            <span className="modalText" >{curData && curData.note}</span>
                          </Flex>
                        </div>

                        <img src={bg1} onClick={this._closeModal}  alt="正在加载过程中"
                        style={{width:'13vw', height:'13vw', marginTop:'8.8vw'}}/>
                      </div>
                    </Modal>
                </Flex>
            );
        } else {
            return (
                <div className="fullBg">
                    <p style={{fontSize: '1.4em'}}>暂无历史记录</p>
                </div>
            )
        }
        
    }
}

class HistoryItem extends Component {
    render(){
        let time = this.props.content.time
        let time0 = moment(time.start).format("YYYY-MM-DD")
        let time1 = moment(time.start).format("HH:mm")
        let time2 = moment(time.end).format("HH:mm")
        let time3 = time1 + '-' + time2 
        return(
          <Flex>
            <Flex 
              direction='column' justify='center'
              className="tabledItem" 
              style={{width:'32.8vw',height:'20vw'}}>
                <span style={{fontSize:'1.2em',lineHeight:'1.6em',color: (this.props.pass > 0)? '#333333' : 'rgba(195, 73, 73, 0.8)'}}>{time0}</span>
                <span style={{fontSize:'1.2em',lineHeight:'1.6em',color: (this.props.pass > 0)? '#333333' : 'rgba(195, 73, 73, 0.8)'}}>{time3}</span>
            </Flex>
            
            <Flex 
              direction='column' justify='center'
              className="tabledItem" 
              style={{width:'38.2vw',height:'20vw'}}>
                <span className="dayText" style={{color: (this.props.pass > 0)? '#333333' : 'rgba(195, 73, 73, 0.8)'}}>{this.props.content.title}</span>
            </Flex>
                        
            <Flex 
              className="tabledItem" justify='center'
              style={{width:'21vw',height:'20vw',background: `url(${(this.props.pass > 0)? bg2 : bg3}) no-repeat right top / 30%`}}>
                <span 
                  className="dayText" 
                  style={{color: (this.props.pass > 0)? '#333333' : 'rgba(195, 73, 73, 0.8)'}}
                  onClick={this.props.clickShow}
                >查看</span>
            </Flex>
          </Flex>
        )
    }
}


