import React, { Component } from 'react';
import { Flex } from 'antd-mobile'
import moment from 'moment';

class NewsItem extends Component {

    render() {
        let time = moment(this.props.time).format('YY-MM-DD')
        return (
            <div style={{width:'100%'}}>
                <Flex justify='between' style={{height:'5.8vh',width:'100%'}}>
                    <span className="listText">{this.props.title}</span>
                    <span className="listText">[{time}]</span>
                </Flex>
                <hr className="listDivider" />
            </div>
        )
    }
}

export default NewsItem;



