import React, { Component } from 'react'
import {Helmet} from "react-helmet"
import { Flex } from 'antd-mobile'
import { withRouter } from 'react-router'
import { getArticle } from '../lib/api'

// import line   from '../assets/headline.png'
// import footer from '../assets/footer.png'
const line = '/static/volunteer/headline.png'
const footer = '/static/volunteer/footer.png'

class Article extends Component {
    constructor(props) {
      super(props);
    
      this.state = {ret1 : null};
    }

    async componentWillMount(){
        let _id = this.props.match.params.id
        let ret1 = await getArticle({_id: _id})
        this.setState({ ret1 });
    }

    render() {
        return (
            <div style={{backgroundColor: '#fff', minHeight:'100vh',paddingTop:'4vw'}}>
                <Helmet><title>志愿者动态</title></Helmet>
                
                <Flex direction='column' align='center' style={{minHeight:'15vw',padding:'0 4vw'}}>
                    <p style={{fontSize:'1.45em',textAlign:'center',lineHeight:'1.6em'}}>{this.state.ret1 && this.state.ret1.title}</p>
                    <img src={line} style={{width:'92vw',height:'4vw',margin:'2vw 0'}}  alt="正在加载中"/>
                </Flex>
                
                <div  
                    style={{margin:'4vw', minHeight: 'calc(100vh - 32.2vw)', width:'92vw'}}
                    className = 'htmlContent'
                    dangerouslySetInnerHTML={{__html: this.state.ret1 && this.state.ret1.content}}
                />

                <div style={{padding:'5.2vw 0 4vw 0', textAlign:'center'}}>
                    <img src={footer} style={{width:'60vw',height:'4vw'}} alt="正在加载中"/>
                </div>
            </div>
        );
    }
}

export default withRouter(Article);



