import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";
import { Flex, Tabs, Badge } from 'antd-mobile';
import { StickyContainer, Sticky } from 'react-sticky';
import { getArticle } from '../../lib/api'
import './style.css'

// import bg1  from '../../assets/sq.png';
const bg1 = '/static/volunteer/sq.png'

const tabs = [
  { title: <Badge>志愿者章程</Badge> },
  { title: <Badge>志愿者管理细则</Badge> },
];
const _prefix = window._config._prefix.link

class Application extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        ret1: null,
        ret2: null
      };
    }

    async componentWillMount(){
        let ret1 = await getArticle({name: 'zyzzc'})
        let ret2 = await getArticle({name: 'zyzglxz'})
        this.setState({
            ret1 : ret1,
            ret2 : ret2,
        });
    }

    renderTabBar = (props) => {
      return (
        <Sticky>
            {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
        </Sticky>
    )}

    render() {
        return (
            <div>
                <Helmet><title>志愿者之家</title></Helmet>

                <Flex justify='center' style={{height:'35.8vh', width:'100vw',textAlign:'center', 
                                               background: `url(${bg1}) center no-repeat / cover`}}>
                    <span style={{height:'11.5vw',width:'37.6vw',background:'#fff',lineHeight:'11.5vw',fontSize:'1.45em',
                        borderRadius:'5.75vw',border:'1.5px solid #000',boxShadow:'0px 0px 2px 2px #fff'
                    }}><Link to={`${_prefix}/resume/0`}>填写申请</Link></span>
                </Flex>

                <div style={{width:'100vw'}} className='tabs'>
                    <StickyContainer>
                        <Tabs tabs={tabs} initialPage={0} renderTabBar={this.renderTabBar}>
                            <div className="article" 
                            dangerouslySetInnerHTML={{__html: this.state.ret1 && this.state.ret1.content}}/>

                            <div className="article" 
                            dangerouslySetInnerHTML={{__html: this.state.ret2 && this.state.ret2.content}}/>
                        </Tabs>
                    </StickyContainer>
                </div>
            </div>
        );
    }
}

export default Application;




