import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { Helmet } from "react-helmet"
import { Flex } from 'antd-mobile'
import { getArticles } from '../lib/api'
import NewsItem from './News/NewsItem'

// import bg1      from '../assets/tdjs.png'
// import bg2      from '../assets/sq.png'
// import bg3      from '../assets/wszyz.png'
const bg1 = '/static/volunteer/tdjs.png'
const bg2 = '/static/volunteer/sq.png'
const bg3 = '/static/volunteer/wszyz.png'

const _prefix = window._config._prefix.link
const _name = window._config.names.news

class Home extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        ret1: null
      };
    }

    async componentWillMount(){
        let ret1 = await getArticles({category:{name: _name}, page:1, limit:6})
        this.setState({
            ret1: ret1 && ret1.data
        });
    }

    render() {
        return (
            <div style={{backgroundColor: '#fff'}}>
                <Helmet><title>志愿者之家</title></Helmet>
                
                <Flex style={{height:'50vh', padding:'4vw'}}>
                    <Link to={`${_prefix}/team`} style={{height:'100%'}}>
                        <div 
                            style={{height:'100%', width:'40vw', 
                            background: `url(${bg1}) center no-repeat / cover`}}
                            className="cardStyle"
                        >
                            <span className="cardName">团队介绍</span>
                        </div>
                    </Link>

                    <Flex justify='between' direction='column'
                        style={{height:'100%', width:'48vw', marginLeft:'4vw'}}>
                        <Link to={`${_prefix}/apply`} style={{height:'47%'}}>
                            <div 
                                style={{height:'100%', width:'48vw', background: `url(${bg2}) center no-repeat / cover`}}
                                className="cardStyle"
                            >
                                <span className="cardName">志愿者申请</span>
                            </div>
                        </Link>

                        <Link to={`${_prefix}/volunteer`} style={{height:'47%'}}>
                            <div 
                                style={{height:'100%', width:'48vw', background: `url(${bg3}) center no-repeat / cover`}}
                                className="cardStyle"
                            >
                                <span className="cardName">我是志愿者</span>
                            </div>
                        </Link>
                    </Flex>
                </Flex>

                <hr className="hrStyle" />

                <div style={{width:'100vw',height:'45vh', padding:'4vw'}}>
                    <Flex justify='between' style={{width:'100%',marginBottom:10}}>
                        <span style={{fontSize:'1.45em'}}>志愿者动态</span>
                        <Link to={`${_prefix}/news`}> <span className="miniBtn">查看更多</span> </Link>
                    </Flex>

                    {
                        (this.state.ret1 && this.state.ret1.length > 0 )? (
                            this.state.ret1.map((item,index)=>{
                                return(
                                    <Link to={`${_prefix}/article/${item._id}`} key={index}>
                                        <NewsItem title={item.title} time={item.createAt} />
                                    </Link>
                                )
                            })
                        ) : (
                            <p style={{textAlign:'center', fontSize:'1.2em',marginTop:'20px',color:'#aaa'}}>暂无数据</p>
                        )
                    }
                </div>
            </div>
        );
    }
}


export default Home;



