import React, { Component } from 'react';
import { Flex, List, InputItem, Checkbox, Modal } from 'antd-mobile'
import './style.css'

const CheckboxItem = Checkbox.CheckboxItem;
const data = [
          { value: 0, label: '平面设计' },
          { value: 1, label: '外语' },
          { value: 2, label: '组织管理' },
          { value: 3, label: '策划' },
          { value: 4, label: '编辑写作' },
          { value: 5, label: '计算机技术' },
          { value: 6, label: '行政秘书' },
          { value: 7, label: '摄影' },
          { value: 8, label: '播音主持' },
          { value: 9, label: '其他' },
        ];
        
class Page4 extends Component {  

    toNextStep = () =>{
        let { datas } = this.props
        let { skill } = datas

        let pass = true
        for (let i = 0; i < skill.length; i++) {
            if (skill[i].key == 1 || skill[i].key == 9) {
                if (!skill[i].content) {
                    pass = false
                    Modal.alert("提示", "外语与其他 详细信息为必填项", [{ text: '确定', style: 'default' }])
                }
            }
        }

        let res = this.props.checkEmpty(['skill'])
        if (!res) {
            pass = false
            Modal.alert("提示", "请至少选择一项技能", [{ text: '确定', style: 'default' }])
        }

        if (pass) {
            this.props.nextStep()
        }
    }

    containItem = (arr, item) =>{
        let hasItem = false
        arr.map(i=>{
            if(i.key == item )
                hasItem = true
            return false
        })
        return hasItem
    }

    removeItem = (arr,item) =>{
        for(let i=0; i<arr.length; i++) {
            if(arr[i].key == item) {
              arr.splice(i, 1);
              break;
            }
        }
        this.props.setData('skill', arr)
    }

    checkItem0 = (value) => {
        let arr = this.props.datas.skill
        if(!this.containItem(arr, value)){
            arr.push({key: value})
            this.props.setData('skill', arr)
        }else{
            this.removeItem(arr, value)
        }
    }

    findItem = (index) => {
        let {skill} = this.props.datas
        for (var i = 0; i < skill.length; i++) {
            if (skill[i].key == index) {
                return skill[i].content
            }
        }
    }

    changeItem = (index, value) => {
        let {skill} = this.props.datas
        for (var i = 0; i < skill.length; i++) {
            if (skill[i].key == index) {
                skill[i].content = value
                this.props.setData('skill', skill)
            }
        }
    }

    addItem = (index, value) => {
        let {skill} = this.props.datas
        let obj = {key: index, content: value}
        skill.push(obj)
        this.props.setData('skill', skill)
    }

    render() {
        let { datas } = this.props
        let { skill } = datas
        let arr = []
        skill.map(item =>{
            arr.push(item.key)
        })
        return (
            <div className="page4">
                <List className="step4">
                    {data.map((i, index)=> {
                        if(index === 1){
                            let value = this.findItem(1)
                            return (
                                <CheckWithInput 
                                    key={1}
                                    label={i.label}
                                    index={index}
                                    values={value}
                                    checked={arr.indexOf(index)>=0}
                                    hint="请填写语种"
                                    _skill={skill}
                                    _changeItem={this.changeItem}
                                    _addItem={this.addItem}
                                    _removeItem={this.removeItem}
                                />
                            )}
                        else if(index === 9){
                            let value = this.findItem(9)
                            return (
                                <CheckWithInput 
                                    key={9}
                                    label={i.label}
                                    index={index}
                                    values={value}
                                    checked={arr.indexOf(index)>=0}
                                    hint="请填写专业技能"
                                    _skill={skill}
                                    _changeItem={this.changeItem}
                                    _addItem={this.addItem}
                                    _removeItem={this.removeItem}
                                />
                            )}
                        else return (
                                <CheckboxItem 
                                    key={i.value} 
                                    checked={arr.indexOf(index)>=0}
                                    onChange={() => this.checkItem0(i.value)}>
                                        {i.label}
                                </CheckboxItem>
                            )
                        
                        })}
                </List>

                <div className="bottom">
                    <div className="tableDivider" />
                    <Flex>
                        <button onClick={this.props.toLastStep} className="bottomBtn">上一步</button>

                        <div style={{width:0, height: '14vw', borderLeft:'1px #707070 solid'}} />

                        <button onClick={this.toNextStep} className="bottomBtn">下一步</button>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Page4;

class CheckWithInput extends Component {

    _input = (v) =>{
        if(v.length != 0){
            this.props._changeItem(this.props.index, v)
        }else{
            this.props._changeItem(this.props.index, '')
        }
    }

    _checked = () =>{
        if(this.props.checked){
            let arr = this.props._skill
            this.props._removeItem(arr, this.props.index)
        }else{
            this.props._addItem(this.props.index, '')
        }
    }

    render(){
        return(
            <Flex >
                <CheckboxItem 
                    checked={this.props.checked}
                    onChange={this._checked}>
                        {this.props.label}
                </CheckboxItem>

                <InputItem 
                    value={this.props.checked ? this.props.values : ''}
                    placeholder={this.props.hint}
                    onChange={this._input}/>
            </Flex>
        )
    }
} 



