import React, { Component } from 'react';
import { Flex, Modal } from 'antd-mobile'

import { getActivity, appointActivity } from '../../lib/api'
import moment from 'moment'
import './style.css'

// import bg1  from '../../assets/headBtn.png';
// import bg2  from '../../assets/left.png';
// import bg3  from '../../assets/right.png';
const bg1 = '/static/volunteer/headBtn.png'
const bg2 = '/static/volunteer/left.png'
const bg3 = '/static/volunteer/right.png'

let now   = new Date(Date.now())
let year  = now.getFullYear()
let month = now.getMonth()
let days  = new Date(year, month+1, 0).getDate() //获取当前月天数（下个月第0天）

export default class Appointment extends Component {

    constructor(props) {
      super(props);

      this.state = {
        year, 
        month,
        days,

        clickDay: null,
        activeData: [],
        fullData: [],
        clickData: []
      };
    }

    componentWillMount(){
        this.getActivities()
    }

    getActivities = () => {
        let activeData = [], fullData = []
        this.setState({
            activeData,
            fullData
        }, () => this.getInfo());
    }

    getInfo = async() => {
        let activeData = [], fullData = []

        let firstDay = new Date(this.state.year, this.state.month, 1)
        let lastDay = new Date(this.state.year, this.state.month+1, 1)
        let ret = await getActivity({time:{
            start: moment(firstDay).valueOf(),
            end: moment(lastDay).valueOf()
        }})

        if (ret && ret.data.length > 0) {
            ret.data.map(item => {
                let today = moment().valueOf()
                let day = moment(item.time.end).valueOf()
                item.activeDay = moment(item.time.end).format('D')
                let datas = item.reservation
                if(day < today){
                    item.canAppoint = false
                    fullData.push(item)
                }else if(datas.count && (datas.count === datas.limit)){
                    item.canAppoint = false
                    fullData.push(item)
                }else{
                    item.canAppoint = true
                    activeData.push(item)
                }
            })
            this.setState({
                activeData,
                fullData
            });
        }
    }

    getDays = () => {
        let arr = [];

        let lastMonth = new Date(this.state.year, this.state.month, 0).getDate();// 上月总共天数
        let weekDay1 = new Date(this.state.year, this.state.month, 1).getDay(); // 本月1号是周几
        let firstDay  = lastMonth - weekDay1 + 1 

        let week2 = new Date(this.state.year, this.state.month, this.state.days).getDay(); // 本月最后一天是周几
        let lastDay   = 6 - week2

        for (let i = firstDay; i<= lastMonth; i++){
            arr.push(i)
        }
        for (let i = 0; i < this.state.days; i++) {
            arr.push(i+1);
        }
        for (let i = 0; i < lastDay; i++) {
            arr.push(i+1);
        }
        return arr;
    }

    toNextMonth = () => {
        if(this.state.month+1 > 11){
            this.setState({
                month: 0,
                days: 31,
                year: this.state.year + 1,
            }, () => this.getActivities())
        }else{
            this.setState({
                month: this.state.month+1,
                days: new Date(year, this.state.month+2, 0).getDate(),
            }, () => this.getActivities())
        }
    }

    toLastMonth = () => {
      if(this.state.month-1 < 1){
        this.setState({
          month: 11,
          days: 31,
          year: this.state.year - 1,
        }, () => this.getActivities())
      }else{
        this.setState({
          month: this.state.month - 1,
          days: new Date(year, this.state.month, 0).getDate(),
        }, () => this.getActivities())
      }
    }

    hasDay = (arr, index) => {
        let hasDay = false
        arr.map(item => {
            if(item.activeDay && (item.activeDay == index.toString())){
                hasDay = true
            }
        })
        return hasDay
    }

    clickItem = e =>{
        const {activeData, fullData} = this.state
        let clickData = []
        activeData.map(item => {
            if(item.activeDay && (item.activeDay == e.toString())){
                clickData.push(item)
            }
        })
        fullData.map(item => {
            if(item.activeDay && (item.activeDay == e.toString())){
                clickData.push(item)
            }
        })
        this.setState({
            clickData 
        });
    }

    render() {
        const {activeData, fullData, clickData} = this.state

        let clickDayItem = clickData.map((item, index) => {
            return(
                <AppointItem canAppoint={item.canAppoint} key={index} time={item.time} title={item.title} _id={item._id}/>
            )
        })

        return (
            <div>
                <p style={{width:'100vw',textAlign:'center',
                fontSize:'1.4em',margin:'5.6vw 0 2vw'}}>服务预约</p>

                <Flex justify='between'
                style={{width:'47.4vw', height:'12.6vw',
                    marginLeft:'26.3vw', padding:'0 8vw',
                    background:`url(${bg1})  center no-repeat / contain`,
                }}>
                    <img src={bg2} style={{height:'3vw'}} onClick={this.toLastMonth} alt="正在加载过程中"/>
                    <span style={{fontSize:'1.4em'}}>{this.state.year}.{this.state.month+1}</span>
                    <img src={bg3} style={{height:'3vw'}} onClick={this.toNextMonth} alt="正在加载过程中"/>
                </Flex>

                <div style={{top:'29.5vw'}} className="tagBlock"> 
                    <div style={{background:'#ffaa5f'}} className="colorBlock"/>
                    <span>可约</span>
                </div>

                <div style={{top:'33.7vw'}} className="tagBlock"> 
                    <div style={{background:'#0c3450'}} className="colorBlock"/>
                    <span>已满</span>
                </div>


                <Flex direction='column' 
                    style={{margin:'4vw',border:'1px solid #333333'}}>
                
                    <Flex style={{width:'92vw',height:'7.6vw'}}>
                        <span className="dayTitle">周日</span>
                        <span className="dayTitle">周一</span>
                        <span className="dayTitle">周二</span>
                        <span className="dayTitle">周三</span>
                        <span className="dayTitle">周四</span>
                        <span className="dayTitle">周五</span>
                        <span className="dayTitle">周六</span>
                    </Flex>

                    <div className="tableLine" />

                    <Flex style={{width:'100%'}} wrap='wrap'>
                    {
                      this.getDays().map((item, index) =>{
                        if(index < 7 && item > 20){ 
                          return(
                            <div key={index} className="dayBlock tabledItem">
                              <span className="dayText" style={{color:'#d3d3d3'}}>{item}</span>
                            </div>
                          )

                        }else if(index > (this.state.days -1) && item < 7){
                          return(
                            <div key={index} className="dayBlock tabledItem">
                              <span className="dayText" style={{color:'#d3d3d3'}}>{item}</span>
                            </div>
                          )

                        }else{
                            if(this.hasDay(activeData, item)){
                                return(
                                  <div key={index} className="dayBlock tabledItem" style={{background:'#ffaa5f'}}>
                                    <span 
                                      className="dayText" 
                                      style={{color:'#fff'}}
                                      onClick={() => this.clickItem(item)}
                                      >{item}</span>
                                  </div>
                                )

                            }else if(this.hasDay(fullData, item)){
                                return(
                                  <div key={index} className="dayBlock tabledItem" style={{background:'#d3d3d3'}}>
                                    <span 
                                      className="dayText" 
                                      style={{color:'#fff'}}
                                      onClick={() => this.clickItem(item)}
                                      >{item}</span>
                                  </div>
                                )

                            }else{
                                return(
                                <div key={index} className="dayBlock tabledItem">
                                  <span className="dayText">{item}</span>
                                </div>
                            )}
                        }
                      })
                    }
                    </Flex>
                </Flex>

                { 
                    clickDayItem
                }
            </div>
        );
    }
}

class AppointItem extends Component {
    appointing = async ()=>{
        let ret = await appointActivity({_id: this.props._id})
        if(ret){
            if(ret.code){
                Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定', style: 'default' }])
            }else{
                Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定', style: 'default' }])
            }
        }else{
            Modal.alert("提示", "服务器出现异常，稍后重试", [{ text: '确定', style: 'default' }])
        }
    }

    render(){
        const appoint = (this.props.canAppoint) ? (
            <div 
                className="cardBlock"
                style={{background:'#ffaa5f'}} 
                onClick={this.appointing}
            >
              <span className="blockText">预约</span>
            </div>
        ) : (
            <div className="cardBlock" style={{background:'#d3d3d3'}}>
              <span className="blockText">已满</span>
            </div>
        )

        let time0 = moment(this.props.time.start).format("YYYY.MM.DD ddd HH:mm")
        let time1 = moment(this.props.time.end).format("HH:mm")
        let time = time0 + '-' + time1
        return(
            <Flex direction='column' align='start' justify='between'
            style={{width:'92vw',height:'18vw',
                padding:'2vw',margin:'4vw',position:'relative',
                boxShadow:'0 2px 2px 1px rgba(12, 52, 80, 0.75)'
            }}>

                <div>
                    <span className="cardText">{time}</span>
                </div>

                <div>
                    <span className="cardText">服务内容：</span>
                    <span style={{width:'15vw', height:'6vw',
                    display:'inline-block', borderRadius:'3vw',
                    border:'1px solid #333333',
                    lineHeight:'6vw',textAlign:'center'
                    }}>{this.props.title}</span>
                </div>
                {appoint}
            </Flex>
        )
  }
}




