import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Helmet } from "react-helmet";
import { Flex, List, InputItem, Button, Modal } from 'antd-mobile'
import { login } from '../../lib/api'
import './style.css'

// import bg1 from '../../assets/login_bg.png';
// import bg2 from '../../assets/login_logo.png';
const bg1 = '/static/volunteer/login_bg.png'
const bg2 = '/static/volunteer/login_logo.png'

const _prefix = window._config._prefix.link

class Volunteer extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        hasError: false,
        idCard  : '',
        name    : ''
      };
    }

    login = async () =>{
      let {hasError, idCard, name} = this.state

      if (idCard.replace(/\s/g, '').length < 18) {
        this.setState({ hasError: true });
      } 
        
      if(!hasError && (name !== '')){
        let ret = await login({idCard: idCard, name: name})
        console.log(ret)
        if(ret.code === true){
          if(ret.level == "apply" || ret.level == "audition" || ret.level == "training" || ret.level == "eliminate"){
            this.props.history.push(`${_prefix}/auditing`)
          }else{
            this.props.history.push(`${_prefix}/daily`)
          }
        }else{
          Modal.alert("提示", <div>{ret.msg}</div>, [{ text: '确定', onPress: () => console.log(1), style: 'default' }])
        }
      }
    }



    onChange0 = (value) => {
        if (value.replace(/\s/g, '').length == 18) {
          this.setState({ hasError: false });
        } 
        this.setState({ idCard: value });
    }

    onChange1 = (value) => {
        this.setState({ name: value });
    }

    render() {
        return (
            <div style={{background:'#fff'}}>
            <div className='inputArea'
            style={{minHeight:'100vh', padding:'8vw 4vw',
            background:`url(${bg1}) bottom  no-repeat / contain`,}}>
                <Helmet><title>志愿者之家</title></Helmet>

                <Flex direction='column'
                    style={{height:'147.2vw',width:'92vw',background:'rgba(255,255,255,0.68)',}}
                >
                    <Flex justify='center'
                        style={{height:'40vw', width:'40vw', margin:'8vw 0 13vw',
                        background:`url(${bg2}) center / cover`,textAlign:'center',}}>
                        <span style={{fontSize:'1.45em'}}>志愿者登录</span>
                    </Flex>

                    <div>
                        <List style={{height:'32vw',width:'75.6vw'}}>
                            <InputItem
                             type='number' maxLength={18}
                             error={this.state.hasError}
                             onErrorClick={this.onErrorClick}
                             onChange={this.onChange0}
                            >身份证</InputItem>

                            <InputItem
                             onChange={this.onChange1}
                            >姓名</InputItem>
                        </List>
                    </div>

                    <Button onClick={this.login}
                    style={{width:'75.6vw',height:'12vw',marginTop:'14.6vw',
                            borderRadius:'6vw',backgroundColor: "#0c3450"
                    }}>登录</Button>
                </Flex>
            </div>
            </div>
        );
    }
}

export default withRouter(Volunteer);




