import React, { Component } from 'react'

// import pageNext from '../../assets/pageNext.png'
// import pageLast from '../../assets/pageLast.png'
const pageNext = '/static/volunteer/pageNext.png'
const pageLast = '/static/volunteer/pageLast.png'

// 分页
class Pagination extends Component{
    constructor(props) {
        super(props);
        this.state={}
    }

    render(){
        let page = {
            display:'inline-block',
            color: '#999999',
            width: '3.4vw',
            height:'3.2vw',
            lineHeight: '4vw',
            fontSize:'3.2vw',
            margin:'0 3vw',
            transition: 'color 0.5s',
        }
        let currentPage = {
            ...page,
            color:'#000000'
        }
 

        return(
            <div style={{
            			textAlign:'center',flexDirection:'row',
            			position:'absolute',bottom:0,
            			width:'92vw',height:'8.8vh',lineHeight:'8.8vh'
          			 }}>

                <img style={{
                        ...page,
                        marginRight:'11.2vw'
                    }} alt="正在加载中"
                    src={pageLast}
                    onClick={()=>{
                        if(this.props.current === 1){
                            return
                        }
                        this.props.onClick(this.props.current-1<1? 1 : this.props.current-1);
                    }}
                    disabled={this.props.current === 1?true:false}
                />

                {
                    (()=>{
                        let { current, total, limit, maxShowPages } = this.props;
                        let pages = Math.ceil(total/limit); // 对浮点数向上取整，总页数

                        let numPage = [];

                        if(pages <= maxShowPages){
                            for (let i = 0; i < pages; i++) {
                                numPage.push(i+1);
                            }
                        }else{
                            for (let i = 0; i < pages; i++) {
                                if(Math.abs(i-current) < maxShowPages / 2){
                                    numPage.push(i+1);
                                }
                            }
                        }
                        if(!numPage.includes(1)){
                            numPage.unshift('...');
                        }
                        if(!numPage.includes(pages)){
                            numPage.push('...');
                        }
                        return numPage.map((item, index)=>
                            (<span
                            		key={index}
                                style={current === item?currentPage:page}
                                onClick={()=>{
                                		if(item !== '...'){
                                    	this.props.onClick(item);
                                		}
                                }}
                                disabled={current === item?true:false}
                            >{item}</span>)
                        )
                    })()
                }

                <img style={{
                        ...page,
                        marginLeft:'11.2vw'
                    }} alt="正在加载中"
                    src={pageNext}
                    onClick={()=>{
                        if(this.props.current === Math.ceil(this.props.total/this.props.limit)){
                            return
                        }
                        let { current, total, limit } = this.props;
                        let page = current+1>Math.ceil(total/limit) ? Math.ceil(total/limit) : current+1
                        this.props.onClick(page);
                    }}
                    disabled={this.props.current === Math.ceil(this.props.total/this.props.limit) ? true : false}
                />
            </div>
        );
    }
}

export default Pagination;



