import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {Helmet} from "react-helmet";
import { Flex } from 'antd-mobile'
import NewsItem   from './NewsItem'
import Pagination from './Pagination'
import { getArticles } from '../../lib/api'

// import line       from '../../assets/headline.png'
const line = '/static/volunteer/headline.png'

const _prefix = window._config._prefix.link
const _name = window._config.names.news

class NewsList extends Component {
    constructor(props) {
      super(props);
    
      this.state = {
        curNum   : 1,
        totalNum : 0,
      };
    }

    makeRemoteRequest = async () =>{
        let ret1 = await getArticles({category:{name: _name}, page: this.state.curNum, limit:13})
        this.setState({ 
            ret : ret1 && ret1.data,
            totalNum : ret1 &&ret1.total 
        });
    }

    clickPage = (num) =>{
        this.setState({
            curNum : num
        }, this.makeRemoteRequest);
    }

    componentWillMount(){
        this.makeRemoteRequest()
    }

    render() {
        return (
            <div style={{margin:'4vw'}}>
                <Helmet><title>志愿者之家</title></Helmet>

                <Flex direction='column' align='center'>
                    <p style={{fontSize:'1.45em'}}>志愿者动态</p>
                    <img src={line} style={{width:'92vw',height:'4vw',margin:'2vw 0'}}  alt="正在加载中"/>
                </Flex>

                <div style={{height:'80vh'}}>
                    {
                        (this.state.ret && this.state.ret.length > 0 )? (
                            this.state.ret.map((item,index) =>{
                                return( 
                                    <Link to={`${_prefix}/article/${item._id}`} key={index}>
                                        <NewsItem title={item.title} time={item.createAt}/>
                                    </Link>
                                )
                            })
                        ) : (
                            <p style={{textAlign:'center', fontSize:'1.2em',marginTop:'20px',color:'#aaa'}}>暂无数据</p>
                        )
                    }
                </div>

                <Pagination 
                    current = {this.state.curNum}
                    total = {this.state.totalNum}
                    limit = {13}
                    maxShowPages = {5}
                    onClick = {this.clickPage}
                />
            </div>
        );
    }
}

export default NewsList;



